import React from "react";

class ParentTest extends React.Component{
  constructor(){
    super();
    this.state = {key:" state key ", count:0};
    this.click = 0;
  };

  changeState = (ev) => {
    this.setState({key:ev.target.value + ++this.click});
  }

  render(){
    return <div>
      <h3>{this.state.key}</h3>
      <button onClick={this.changeState} value="   = ">()</button>
      <hr />
      <ChildTest key={this.state.key} changeState={this.changeState} />
    </div>
  }  
};

class ChildTest extends React.Component{
  constructor(){
    super();
    this.state = {key2:" state key ", count:0};
    this.click = 0;
  }

  changeState2 = (ev) => {
    this.setState({key2:ev.target.value + ++this.click});
  }

  render(){
    return <div>
      <h3>  props = {this.props.key}</h3>
      <button onClick={this.props.changeState} value="   = ">(props (state) )</button>
      <h3> state = {this.state.key2}</h3>
      <button onClick={this.changeState2} value="   = ">()</button>
    </div>
  }  
};

export {ParentTest};