import { useState } from "react";
import styled from "styled-components";

const StyledButton = styled.button`
  color: ${(p)=>p.color || 'black'};
  background-color: ${(p)=>p.background || 'white'};
  border: 1px solid red;
`;

function NumberList(){
    // - [1,2,3,4,5]  
    //1.    - <ul><li> 
    //2.  1  text input
    //3. button 3 -  , ,  - Styled  
    //        
    //        
    //      
    //         
  const [array, setArray] = useState([1, 2, 3, 4, 5]);
  const [input, setInput] = useState('');
  // const print = <ul>{array.map((item) => {
  //   return <li>{item}</li>;
  // })}</ul>;
  return <div>
    <ul>
      {array != undefined ? array.map((item) => <li>{item}</li>) : ''}
    </ul>
    <StyledButton onClick={() => setArray([...array, input])}></StyledButton>
    <StyledButton onClick={() => setArray([...array.slice(0, -1), input])}></StyledButton>
    <StyledButton onClick={() => setArray(array.slice(0, -1))}></StyledButton>
    <br />
     <input type="text" value={input} onChange={(e) => setInput(e.target.value)} />
  </div>;
}

export default NumberList;