import React, { useState } from "react";

class State_User1 extends React.Component{
  constructor(){
    super();
    this.state = {id:"id ", name:"name "};
    this.dept = "dept ";
  }
  
  render(){
    let handlerfunction = () => {
      this.setState({id:"id ", name:" name"});
    };
    return <div>
      <h3>id={this.state.id}</h3>
      <h3>name={this.state.name}</h3>
      <button onClick={handlerfunction}></button>
      </div>;
  }
}

function State_User2(){
  // let [id, setId] = useState("");
  // let handlerfunction = () => {
  //   setId("  ");
  // };
  // return <div>state={id}<button onClick={handlerfunction}></button></div>
  let [obj, setObj] = useState({id:"id", count:100});
  let handlerfunction = function(){
    setObj({...obj, count:1000});
  };
  return <div>state1={obj.id}, state2={obj.count}<button onClick={handlerfunction}></button></div>
};

export {State_User1, State_User2};