import React from "react";

function Greeting (){
  let insa1 = "   ";
  let insa2 = " ";
  let insa3 = "  ?";
  let random = parseInt(Math.random() * 3);
  let result;
  if (random == 0){
    result = insa1;
  } else if (random == 1) {
    result = insa2;
  } else {
    result = insa3;
  }

  return <h1>{random} = {result}</h1>
}

class Greeting2 extends React.Component{
  constructor(){
    super();
    this.insa1 = "   ";
    this.insa2 = " ";
    this.insa3 = "  ?";
    this.random = parseInt(Math.random() * 3);
  }
  render(){
    return <h1>{this.random == 0? this.insa1 : this.random == 1? this.insa2 : this.insa3}</h1>;
  }
}

export {Greeting as default, Greeting2};